import React, { useState } from 'react';
import './DiarySetUp.scss';
import { getCookie } from '../../../api/cookie';
import { useMutation } from '@tanstack/react-query';
import { makeNewTrip } from '../../../api/DiaryApis';
import { useNavigate } from 'react-router-dom';

export default function DiarySetUp() {
  const budget = [
    '(USD)',
    '(JPY)',
    '(EUR)',
    '(CNY)',
    '(HKD)',
    '(THB)',
    '(AUD)',
    '(CAD)',
    '(GBP)',
    '(SGD)',
    '(TWD)',
    '(CHF)',
    '(MYR)',
    '(PHP)',
    '(VND)',
    '(NZD)',
    '(IDR)',
    '(INR)',
    ' (AED)',
  ];

  const [tripInfo, setTripInfo] = useState({
    prjName: '',
    location: '',
    currency: '(USD)',
    budget: '',
    startDate: '',
    endDate: '',
    exchangedBudget: '',
  });

  //  
  const userName = decodeURI(getCookie('name'));
  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setTripInfo((trip) => ({ ...trip, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    makeTripDiary.mutate();
  };

  //   
  const makeTripDiary = useMutation(() => makeNewTrip({ tripInfo }), {
    onSuccess: (data) => {
      alert('');
      navigate(`/trips/${data.data.tripId}/dailies`, { state: tripInfo });
    },
    onError: () => {
      console.log('  .');
    },
  });

  return (
    <>
      <main className="diarySetupMain">
        <h1 className="diaryH1"></h1>
        <h2 className="diaryH2">{userName}</h2>
        <form className="diaryForm" action="POST" onSubmit={handleSubmit}>
          <figure className="logoArea">
            <img className="logo" src="/assets/icons/shinhan-symbol.webp" alt="logo" />
            <figcaption>Triplet</figcaption>
          </figure>
          <section>
            <label htmlFor="prjName">  </label>
            <input
              type="text"
              id="prjName"
              name="prjName"
              value={tripInfo.prjName}
              onChange={handleChange}
              placeholder=" "
              required
            />
          </section>
          <section>
            <label htmlFor="currency"> </label>
            <select
              name="currency"
              id="currency"
              value={tripInfo.currency !== '' ? tripInfo.currency : budget[0]}
              onChange={handleChange}
              required
            >
              {budget.map((v, i) => (
                // selected 
                <option key={i} value={v}>
                  {v}
                </option>
              ))}
            </select>
          </section>
          <section>
            <label htmlFor="tripDate" className="tripDate">
                
            </label>
            <input
              type="date"
              id="tripDate"
              name="startDate"
              value={tripInfo.startDate}
              onChange={handleChange}
              required
            />
            <input
              type="date"
              id="tripDate"
              name="endDate"
              min={tripInfo.startDate}
              value={tripInfo.endDate}
              onChange={handleChange}
              required
            />
          </section>
          <section>
            <label htmlFor="location"> </label>
            <input
              type="text"
              id="location"
              name="location"
              placeholder=" "
              value={tripInfo.location}
              onChange={handleChange}
              required
            />
          </section>
          <section>
            <label htmlFor="budget"> </label>
            <input
              type="text"
              id="budget"
              name="budget"
              placeholder="250,000"
              value={tripInfo.budget}
              onChange={handleChange}
              required
            />
          </section>
          <section>
            <label htmlFor="budget"> </label>
            <input
              type="text"
              id="exchangedBudget"
              name="exchangedBudget"
              placeholder=""
              value={tripInfo.exchangedBudget}
              onChange={handleChange}
              required
            />
          </section>
          <button className="makeDiary"></button>
          <section className="boardingArea">
            <span className="boardingArea">boarding pass</span>
          </section>
        </form>
        <section className="backgroundForm">
          <section className="backgroundFooter">Boarding Pass</section>
        </section>
        <img className="airplaneLogo" src="/assets/airplane.webp" alt="airplane" />
      </main>
    </>
  );
}
